/*@import url('https://fonts.googleapis.com/css?family=Sigmar+One');*/

[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

html {
    font-size: 100%;
    background-color: #D7EAE1;
    background-color: #A0CDB9;
}

body {
    background-color: #D7EAE1;
    color: #474747;
    background-color: #A0CDB9;
}

p {
    font-size: 1em;
}

@media (min-width: 64em) {
    html {
        font-size: 112.5%;
    }
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
}

.allwidth {
    width: 100%;
    margin: 0 auto;
}

.game {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.board-row::after {
    content: "";
    display: block;
    clear: both;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

h1, h2, h3, h4, h5, h6, .red-small {
    color: #AD2B00;
}

.larger-font {
    font-size: 1.5em;
}
