body {
    background-color: #A0CDB9;
}

.hub-page, .zones-page, .login-page {
    width: 80%;
    padding: 10px;
    text-align: center;
    margin: 0 auto;
}

.buttonstyle {
    color: #474747;
}

.btn-primary {
    width: 100%;
    background-color: #f8f9fa;
    color: #212529;
    border: 1px solid transparent;
    font-weight: 400;
    transition: color .15s;
    padding: .5rem 1rem;
    font-size: 1.25rem;
    line-height: 1.5;
    border-radius: .3rem;
    margin-bottom: .5rem;
}

.btn-primary:hover, .btn-primary:not(:disabled):not(.disabled):active {
    border: 1px solid #AD2B00;
    box-shadow: 0 0 0 2px #AD2B00;
    background-color: #f8f9fa;
    color: #474747;
    color: #AD2B00;
    background-color: #F0F2F4;
}

.redbutton,
.show>.btn-primary.dropdown-toggle,
.show>.btn-primary.dropdown-toggle:active,
.show>.btn-primary.dropdown-toggle:focus,
.btn-primary:not(:disabled):not(.disabled).active {
    color: #AD2B00;
    border-color: #ad2b00;
    background-color: #F8F9FA;
    background-color: #F0F2F4;
}


.flex-column, .btn-group-toggle>.btn, .btn-group>.btn:not(:first-child) {
    margin-bottom: .5rem;
}

div.preserve-margin {
    margin-bottom: .5rem;
}

.btn-group>.btn:not(:last-child):not(.dropdown-toggle).active {
    background-color: #AD2B00;
    color: #F8F9FA;
    color: #AD2B00;
    background-color: #F0F2F4;
}


/* Remove outline for non-keyboard :focus */
*:focus:not(.focus-visible) {
  outline: none;
}

/* Optional: Customize .focus-visible */
.focus-visible,
.btn-primary:not(:disabled):not(.disabled):active:focus,
.show>.btn-primary.dropdown-toggle:focus,
.btn-primary:focus {
  box-shadow: 0 0 0 0.2rem #AD2B00;
}

.react-datepicker-wrapper {
  width: 100%;
}

.react-datepicker__input-container {
  width: 100%;
}

.datepickerWrap {
    box-sizing: border-box;
    width: 100%;
}

.redtext {
    color: #AD2B00;
    font-size: 1.5rem;
}

.twofive {
    font-size: 1.25rem;
}

.tempis, .htempis {
    color: #5F8745;
    color: #293f1a;
    color: #51723a;
}

.htempis {
    float: left;
    padding-left: 10%;
}

.hshould {
    float: right;
    padding-right: 10%;
}

.should, .hshould {
    color: #2F5C6A;
}

.zone {
    color: #AD2B00;
}

.tempis, .should, .zone {
    width: 33%;
}

.three, .four {
    display: flex;
    flex-flow: row wrap;
}

.dropmax, .dropmin, .dropdsm, .dropaway {
    width: 23.5%;
}

.dropmax {
    margin-left: 2%;
}

.dropdsm {
    margin-right: 2%;
}

.dropaway {
    margin-right: 2%;
}


.form-control-plaintext {
    height: 2.25rem;
    font-size: 1.5rem;
    text-align: center;
    color: #212529;
    border: none;
    background-color: #f8f9fa;
}

.dropdown {
    margin-top: .5rem;
}

.gray {
    color: #6c757d;
}

.half {
    width: 50%;
    float: left;
}

@media (min-width: 1400px) {
    .container {
        max-width: 1370px;
    }
}


@media (max-width: 550px) {
    .half {
        width: 100%;
    }
    .dsm, .min, .max, .away {
        font-size: 1rem;
    }

    .dsm > .redtext, .min > .redtext, .max > .redtext, .away > .redtext {
        font-size: 1.5rem;
    }
}

@media (max-width: 400px) {
    .dsm, .min, .max, .away {
        font-size: 0.8rem;
    }

    .dsm > .redtext, .min > .redtext, .max > .redtext, .away > .redtext {
        font-size: 1.2rem;
    }

    .four > .dropdown > button > .btn-primary {
        padding: 0;
    }
}

.container {
    width: 100%;
}

.mytable {
    font-size: 0.6rem;
    margin-top: 30px;
}

.mytable > thead {
    background-color: #ADD8E6;
}

.starttable {
    margin-top: 30px;
}

.mytable > tbody, .starttable > tbody {
    background-color: #f8f9fa;
}

.mainroom {
    width: 34%;
}

.current, .want {
    width: 33%;
}

.chart {
    height: 300px;
}

.chartnow, .charttemp {
    height: 200px;
}

.class0 {
    background-color: #A0CDB9;
}

.class1 {
    background-color: #69A7BA;
}

.class2 {
    background-color: #AD2B00;
}

.class3 {
    background-color: yellow;
}

.dropContainer {
    width: 100%;
    background-color: #f8f9fa;
    margin-bottom: .5rem;
    height: 54px;
    border-radius: 4px;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.settings-toggle input[type=checkbox] {
    display: none;
}

.d-flex label.settings-toggle {
    width: 90%;
    line-height: 54px;
    font-size: 1.25rem;
}

.d-flex label.settings-toggle.btn-danger {
    margin-right: 0;
    margin-left: 10%;
    height: 54px;
    justify-content: center;
    align-items: center;
}

.d-flex label.settings-toggle.btn-success {
    margin-left:-10%;
    padding: .5rem, 1rem;
    justify-content: center;
    align-items: center;
}

.btn-success {
    background-color: #51723a;
    background-color: #78af4b;
}

.btn-danger {
    background-color: #AD2B00;
}