* {
    box-sizing: border-box;
    webkit-box-sizing: border-box;
}

.container {
    max-width: 95%;
    margin: 0 auto;
}

a {
    color: #00b7ff;
}

.rpio-page {
    padding-left: 50px;
    padding-right: 50px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 20px;
}

.row {
    margin-right: -15px;
    margin-left: -15px;
}

.center_img {
    margin: 0 auto;
}

div.row > div[class^="col-"]:nth-child(odd) {
    border-right: 1px solid white;
}

div.row > div[class^="col-"]:nth-child(even) {
    border-left: 1px solid white;
}

div[class^="col-"] {
    padding: 4px 8px;
    background-color: #5F8645;
    color: #e9e5d2;
    line-height: 34px;
    margin-bottom: 2px;
    height: 43px;
}

div.row > div.pin {
    background-color: #000;
    color: #fff;
}

div.row > div.pin span.pin {
    border: 5px solid white;
    border-radius: 50px;
    padding: 4px;
}

div.row > div.pin span.pin.first {
    border-radius: 0px;
}

div.row > div.pin span.pin.gnd {
    border-color: gray;
}

div.row > div.pin span.pin.power3v {
    border-color: darkred;
}

div.row > div.pin span.pin.power5v {
    border-color: red;
}

div.row > div.pin span.pin.gpio {
    border-color: green;
}

div.row > div.pin span.pin.i2c {
    border-color: yellow;
}

.text-center {
    text-align: center;
}

.buttonstyle {
    color: #474747;
}

.pull-right {
    float: right!important;
    height: 100%;
}

.rpio-toggle,
.mode-toggle,
.mode-toggle-right,
.sensor-dd-left,
.sensor-dd-right,
.gpio-temp,
.gpio-temp-right {
    width: 58px;
    height: 100%;
    background-color: white;
}

.rpio-toggle.btn,
.mode-toggle.btn,
.mode-toggle-right.btn,
.sensor-dd-left.btn,
.sensor-dd-right.btn,
.gpio-temp,
.gpio-temp-right {
    margin-bottom: .5rem;
    border: none;
    padding: 0;
}

.temp {
    font-size: .8rem;
}

.mode-toggle, .sensor-dd-left, .gpio-temp {
    margin-right: 5px;
}

.mode-toggle-right, .sensor-dd-right, .gpio-temp-right {
    margin-left: 5px;
}

.toggle, .gpio-temp, .gpio-temp-right {
    position: relative;
    overflow: hidden;
}

.toggle input[type=checkbox] {
    display: none;
}



.btn {
    white-space: nowrap;
    cursor: pointer;
    touch-action: manipulation;
    border: 1px solid transparent;
    border-radius: 4px;
    vertical-align: middle;
}

.btn-success, .btn-danger, .btn-light {
    padding: 0;
    border: none;
    line-height: 35px;
}

.btn-success {
    margin-left: -13px;
}

.btn-danger {
    margin-left: 13px;
}



label.toggle {
    width: 45px;
    margin-bottom: 0;
}

.dropdown-sensors, .dropdown-sensors-right {
    height: 0;
}


.dropdown-sensors-right button.btn-primary,
.dropdown-sensors button.btn-primary {
    font-size: 0.7rem;
    padding: 0;
    line-height: 0.9rem;
}

.dropdown-sensors-right.dropdown,
.dropdown-sensors.dropdown {
    margin-top: 0;
}

@media (min-width: 1200px) {
    .col-lg-5 {
        width: 41.67%;
        float: left;
        position: relative;
        min-height: 1px;
    }
}

@media (min-width: 915px) {
    .rpio-page, .zone-page, .login-page {
        width: 95%;
        margin: 0 auto;
    }
    .col-md-5 {
        -ms-flex: 0 0 41.666667%;
        flex: 0 0 41.666667%;
        max-width: 41.666667%;
    }
}


@media (max-width: 990px) {
    .rpio-page {
        padding: 2px;
    }
    .col-sm-1.hidden-xs {
        display: none;
    }
    .col-md-5 {
        max-width: 50%;
        -ms-flex: 0 0 50%;
        flex: 0 0 50%;
    }
}

@media (max-width: 800px) {
    .sensor-dd-left.btn,
    .sensor-dd-right.btn {
        width: 35px;
    }
    .rpio-toggle,
    .mode-toggle,
    .mode-toggle-right,
    .sensor-dd-left,
    .sensor-dd-right,
    .gpio-temp,
    .gpio-temp-right {
        width: 40px;
    }
    label.toggle {
        width: 35px;
    }
    .btn-success {
        margin-left: -5px;
    }
    .btn-danger {
        margin-left: 5px;
    }
    .hub-page {
        width: 100%;
    }
}


@media (max-width: 650px) {
    .col-md-5 {
        max-width: 100%;
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
    }
    .htempis {
        padding-left: 0;
        padding-right: 2%;
    }
    .hshould {
        padding-right: 0;
        padding-left: 2%;
    }
    h {
        font-size: 2rem;
    }
}

@media (max-width: 550px) {
    .htempis {
        padding: 0;
        width: 100%;
    }
    .hshould {
        padding: 0;
        width: 100%;
    }
    .tempis, .should, .zone {
        width: 100%;
        padding: 0;
    }
}
